import { VestSchema, getStsCountFromChartRow } from "@tanyoknits/shared";
import { overview } from "./pattern";
import { getPatternByPartGroup, getGaugeItems } from "./sizes";
import { getMotifRepeatsBySize } from "./motifs";
import { vestPartsInstruction } from "../../patternMakers/vest";
import OverviewWithSchema from "../../components/OverviewWithSchema";
import { Flex } from "@chakra-ui/react";
import MotifRepeatsPattern from "../../components/MotifRepeatsPattern";
import PatternPartsGroup from "../../components/PatternGroupedParts";
import profile from "./profile";
import PageContentWrapper from "../../layout/PageContentWrapper";

interface Props {
  menu: number;
  size: number;
}

// By size
const MOTIF_OFFSET = [0, 2, 2, 4, 6, 4, 6, 4, 4];

export default function Instruction({ menu, size }: Props) {
  const { frontChart, backChart, motifs } = getMotifRepeatsBySize(size);

  // sts count
  const halfBodySts = getStsCountFromChartRow(frontChart[0]);
  // get size
  const stsGauge = profile.gauge.sts_count / 4;
  const rowGauge = profile.gauge.row_count / 4;
  const gaugeItems = getGaugeItems(size, halfBodySts, stsGauge, rowGauge);
  const [back, front, body, band] = getPatternByPartGroup(
    backChart,
    frontChart,
    stsGauge,
    rowGauge,
    gaugeItems,
    profile.needles[0].size,
    profile.needles[1].size,
    MOTIF_OFFSET[size]
  );

  let content = null;
  if (menu === 0) {
    // overview with motif repeats
    content = (
      <Flex direction="column" gap={4}>
        <OverviewWithSchema overview={overview} parts={vestPartsInstruction}>
          <VestSchema viewBoxRatio={1.6} neck="round" />
        </OverviewWithSchema>
        <MotifRepeatsPattern chart={frontChart} motifList={motifs} size="S" />
      </Flex>
    );
  } else if (menu === 1) {
    // back top
    content = <PatternPartsGroup {...back} />;
  } else if (menu === 2) {
    content = <PatternPartsGroup {...front} partStartID={3} />;
  } else if (menu === 3) {
    content = <PatternPartsGroup {...body} partStartID={6} />;
  } else {
    content = <PatternPartsGroup {...band} partStartID={7} />;
  }
  return <PageContentWrapper>{content}</PageContentWrapper>;
}
