import React from "react";
import { Box, Flex, Text } from "@chakra-ui/react";
import { SchemaPartInstruction } from "../utils/types";

interface Props {
  parts: SchemaPartInstruction[];
}

export default function PartTextList({ parts }: Props) {
  return (
    <Box>
      {parts.map((item, i) => (
        <React.Fragment key={i}>
          {item.group != null && (
            <Text fontWeight="bold" mb={2} mt={i > 0 ? 4 : 0}>
              {item.group}
            </Text>
          )}
          <Flex
            flexDirection={["column", "row"]}
            mb={[4, 2]}
            gap={2}
            align="top"
          >
            <Text
              variant={"code-highlighted"}
              width="fit-content"
              height="fit-content"
            >
              Part {`${i + 1}`}
            </Text>
            <Text>({item.direction})</Text>
            <Text>{item.instruction}</Text>
          </Flex>
        </React.Fragment>
      ))}
    </Box>
  );
}
